.Container > h3 {
    margin-top: 50px;
    padding: 20px;
    margin-left: 85px;
    font-weight: 700;
    font-size: 20px;
  }
  
  .listedHouses {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    padding: 20px;
    margin-top: 10px;
  }
  
  .img-container {
    margin-bottom: 10px;
  }
  
  .img {
max-width: 300px;
max-height: 200px;
border-radius: 10%;
  }
  
  .loadingScreen {
    width: 55%;
    padding-left: 20%;
  }
  
  .House__priceAndDetails {
    margin-bottom: 40px;
  }
  .House__price {
    font-size: 18px;
    font-weight: 500;
  }
  
  .House__snipDescription {
    font-size: 15px;
    font-weight: 500;
  }
  
  @media only screen and (max-width: 400px) {
    .img-conatiner > img {
      width: 350px;
      height: 200px;
    }
  }