.Footer__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
    background-color: #f5f5f5;
  }
  
  .Footer__companyBrand {
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 4px;
  }
  
  .Footer__licensing {
    margin-bottom: 15px;
  }
  
  @media only screen and (max-width: 400px) {
    .Footer__container {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 50px;
      background-color: #f5f5f5;
      width: 460px;
    }
  }